import React from 'react';
import { navigate } from 'gatsby';
import Img from 'gatsby-image';

import GetDemoButton from './GetDemoButton';
import Button from './Button';

import { colors, screen } from '../constants';

export default function BlockHeaderWithImage({
  title = '',
  desc = '',
  image,
  subtitle = '',
  secondaryButtonText = '',
  secondaryButtonTo = '/',
  primaryButtonText = '',
  primaryButtonTo = '/',
}) {
  function handleSecondaryButtonTextClick() {
    navigate(secondaryButtonTo);
  }

  return (
    <section className="block-header-with-image">
      <div className="block-header-with-image__content">
        <div>
          <span className="block-header-with-image__subtitle app__pretitle">
            {subtitle}
          </span>
          <h1 className="app-hero__title">{title}</h1>
          <p className="app-hero__desc">{desc}</p>
          <div className="app__actions">
            {primaryButtonText && <GetDemoButton text={primaryButtonText} />}
            {secondaryButtonText && (
              <Button large onClick={handleSecondaryButtonTextClick}>
                {secondaryButtonText}
              </Button>
            )}
          </div>
        </div>
        <Img
          fluid={image}
          alt="block how header"
          className="app__image block-header-with-image__image"
        />
      </div>
      <style jsx>{`
        .block-header-with-image__subtitle {
          text-transform: uppercase;
          font-size: 16px;
          font-weight: 500;
          line-height: 2.5;
          letter-spacing: 1.3px;
          color: ${colors.title};
        }
        .block-header-with-image__content {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: flex-start;
          margin-top: 64px;
        }
        .block-header-with-image__content > div {
          flex: 1;
          margin-right: 30px;
        }
        .block-header-with-image__content .app-hero__desc,
        .block-header-with-image__content .app-hero__title {
          max-width: 646px;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-header-with-image__content > div {
            flex: initial;
            margin-right: 0;
          }
          .block-header-with-image__content {
            flex-direction: column-reverse;
            margin-top: -64px;
          }
          .block-header-with-image__content > div {
            margin-top: -170px;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-header-with-image__image {
          max-width: 480px;
          width: 100%;
          clip-path: polygon(0px 140px, 625px 0px, 480px 550px, 0px 660px);
          position: relative;
          top: -20px;
          margin: 0 auto;
        }

        @media (max-width: 1140px) {
          .block-header-with-image__image {
            max-width: 400px;
            clip-path: polygon(0px 140px, 625px 0px, 480px 420px, 0px 530px);
          }
        }
        @media (max-width: ${screen.mobileMax}px) {
          .block-header-with-image__image > div {
            margin-top: -40px;
          }
          .seller__image {
            max-width: 240px;
            clip-path: polygon(0px 89px, 368px 0px, 385px 145px, 0px 230px);
            top: 30px;
          }
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-header-with-image__image {
            max-width: 480px;
            clip-path: polygon(0px 175px, 745px 0px, 480px 355px, 0px 465px);
            position: relative;
            top: -20px;
          }
        }
      `}</style>
    </section>
  );
}
