import React from 'react';

import { screen, fonts } from '../constants';
import Img from 'gatsby-image';

export default function Block4Part({ pretitle, title, desc, items }) {
  return (
    <div className="block-4-part">
      {pretitle && (
        <div className="block-4-part__pretitle app__pretitle">{pretitle}</div>
      )}
      {title && <h1 className="block-4-part__title app__title">{title}</h1>}
      {desc && <div className="block-4-part__desc app__desc">{desc}</div>}

      <div className="block-4-part__items app__row">
        {items.map(item => (
          <div key={item.title} className="block-4-part__item">
            {item.image && (
              <Img
                fluid={item.image}
                imgStyle={{ objectFit: 'contain' }}
                alt={item.title}
                className="app__image block-4-part__image"
              />
            )}
            {item.icon}
            {item.pretitle && (
              <h1 className="block-4-part__item-pretitle">{item.pretitle}</h1>
            )}
            <h3 className="block-4-part__item-title">{item.title}</h3>
            <div className="block-4-part__item-content">{item.content}</div>
          </div>
        ))}
      </div>
      <style jsx>{`
        .block-4-part {
          justify-content: center;
          text-align: center;
        }

        .block-4-part__title {
          max-width: 800px;
          margin-right: auto;
          margin-left: auto;
        }

        .block-4-part__desc {
          max-width: 800px;
          margin-right: auto;
          margin-left: auto;
          margin-bottom: 50px;
        }

        .block-4-part__item {
          text-align: center;
          max-width: 300px;
          margin-right: 69px;
        }

        .block-4-part__item:last-child {
          margin-right: 0;
        }

        .block-4-part__items {
          margin-top: 80px;
          justify-content: center;
        }

        .block-4-part__item-title {
          font-family: ${fonts.gilroy};
          font-size: 24px;
          font-weight: 600;
          line-height: 1.33;
          letter-spacing: 0px;
        }
        .block-4-part__item-content {
          font-size: 20px;
          line-height: 1.4;
          letter-spacing: 0.7px;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-4-part__title {
            max-width: 400px;
            margin-left: auto;
            margin-right: auto;
          }
          .block-4-part__items {
            align-items: center;
          }
          .block-4-part__item {
            text-align: center;
            max-width: 400px;
            margin: 0 0 69px 0;
          }

          .block-4-part__item:last-child {
            margin-bottom: 0;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-4-part__item-pretitle {
          font-family: ${fonts.gilroy};
          font-size: 55px;
          font-weight: 600;
          line-height: 1;
          letter-spacing: 0px;
        }

        .block-4-part__item-pretitle span {
          font-size: 38.5px;
          font-weight: 500;
        }

        .block-4-part__image {
          max-width: 150px;
          margin: 0 auto;
        }
      `}</style>
    </div>
  );
}
